import React from "react";
import { Radio } from "antd";

export default function ScheduleTypeSelector(props) {
  return (
    <Radio.Group
      {...props}
      size="small"
      buttonStyle="solid"
    >
      <Radio.Button value="simple">Simple</Radio.Button>
      <Radio.Button value="continuous">Continuous</Radio.Button>
      <Radio.Button value="cron">Cron</Radio.Button>
      <Radio.Button value="oneTime">One Time</Radio.Button>
    </Radio.Group>
  );
}
